<template>
	<view class="page">
		<view class="content-title">
			<view class="first">
				<image style="width: 64rpx;height: 108rpx;" src="../../static/classFirst@2x.png"></image>
				<text style="font-size: 32rpx;color: #272424;font-weight: 500;">选择您要设计的女装类目</text>
			</view>
			<view class="second">
				<image style="width: 64rpx;height: 108rpx;" src="../../static/classSecond@2x.png"></image>
				<text style="font-size: 32rpx;color: #272424;opacity: 0.5;">上传</text>
			</view>
		</view>
		<view>
			<image class="content-img" src="https://img.yifuapp.wang/img/bigImg2@2x.png"></image>
			<view class="content-back">
				<view class="section" @click="itemClick(1)"></view>
				<view class="section" @click="itemClick(2)"></view>
				<view class="section" @click="itemClick(3)"></view>
				<view class="section" @click="itemClick(4)"></view>
			</view>
		</view>
	</view>
</template>

<script>
	import Api from "@/utils/api.js"
	import httpApi from "@/api/aili/index.js"
	import Config from "@/config.js"
	export default {
		data() {
			return {
				shop_type: [],
				shop_tag_type: [],
				designInfo: null,
			}
		},
		onLoad() {
			let queryData = uni.getStorageSync(this.$config.storage.queryData)
			let shop_tag = queryData["shop_tag"]

			this.shop_type = queryData["shop_type"]
			this.shop_tag_type = shop_tag.filter(item => {
				return item.parent_id === "0"
			})
		},
		methods: {
			itemClick(index) {
				let type_name = ""
				let filter_id = []
				let id = ""
				switch (index) {
					case 1:
						type_name = "上衣"
						id = 2
						filter_id = [102, 103, 106, 109, 110, 114]
						break;
					case 2:
						type_name = "裤子"
						id = 4
						filter_id = [101, 102, 104, 105, 106, 107, 108, 109, 111, 112, 113]
						break;
					case 3:
						type_name = "半身裙"
						id = 24
						filter_id = [101, 103, 104, 105, 106, 107, 108, 110, 111, 112, 113]
						break;
					case 4:
						type_name = "连衣裙"
						id = 23
						filter_id = [101, 103, 105, 107, 108, 110]
						break;
				}
				//选择的类目
				let arr = this.shop_type.filter(item => {
					return item.type_name == type_name
				})
				if (arr.length > 0) {
					//保存选择的类目
					this.$config.design.select_shop_type = arr[0]
				}

				//其它类目
				this.$config.design.shop_tag_types = this.shop_tag_type.filter(item => {
					return !filter_id.includes(parseInt(item.id))
				})

				this.$nav.to("/pages/shouye/designDetails", {
					detail: JSON.stringify(this.$config.design.select_shop_type),
					shopTypeId: arr[0].id
				}, false)
			}
		}
	}
</script>

